<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // text-align: center;
  color: #2c3e50;
  display: flex;
  h2 {
    margin: 0;
  }
}
#nav {
  width: 180px;
  border-right: 1px solid red;
  padding: 10px;
  flex-flow: 0;
  flex-shrink: 0;
  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>
<template>
  <div id="app">
    <div id="nav">
      <select v-model="selectedServer" @click="switchServer">
        <option v-for="(server, idx) of servers" :key="idx" :value="server">
          {{ server || "" }}
        </option>
      </select>
      {{ connectionStatus }}
      <router-link to="/"
        >正在下载{{
          -(-globalStat.numWaiting - globalStat.numActive)
        }}</router-link
      >
      |
      <router-link to="/completed">完成{{ globalStat.numStopped }}</router-link>
      | <router-link to="/new">新建下载</router-link> |
      <router-link to="/settings">设置</router-link>|
      <router-link to="/servers">服务器</router-link>|
    </div>
    <router-view :aria2="aria2" @servers-changed="server = $event" />

    <div class="globalStat">
      全局速度 上传:{{ globalStat.uploadSpeed }} 下载:{{
        globalStat.downloadSpeed
      }}
    </div>
  </div>
</template>

<script>
import Aria2Client from "./aria2-client.js" //转换服务器
import Vue from "vue"

export default {
  data() {
    var servers = JSON.parse(localStorage.getItem("aria2Servers")) || []

    return {
      globalStat: {},
      servers: servers,
      selectedServer: null,
      connectionStatus: "connecting...",
      aria2: null,
    }
  },
  methods: {
    // 循环写法
    // async startIntervalUpdat2() {
    //     try {
    //       for (;;) {
    //         this.globalStat = await this.aria2.getGlobalStat()
    //         await delay(1000) // 自建函数
    //       }
    //     }catch(e) {
    //       if (e == 'WS_CONNECTION_ERROR') {
    //         this.$alert('连接失败')
    //       } else {
    //         throw e
    //       }
    //     }
    // },
    startIntervalUpdate() {
      this.intervalId = setInterval(async () => {
        try {
          console.log('1 startIntervalUpdate')
          this.globalStat = await this.aria2.getGlobalStat()
          console.log('2 startIntervalUpdate')
        } catch(e) {
          console.log('3 catch(e) startIntervalUpdate')
          if (e == 'WS_CONNECTION_ERROR') {
            this.$alert('连接失败')
          } else {
            throw e
          }
        }
      },1000)
    },
    async switchServer() {
      // 转换服务器
      console.log("关闭现有服务器")
      if (Vue.prototype.aria2) {
        Vue.prototype.aria2.close()
      }

      this.globalStat = {}

      this.connectionStatus = "connecting..."

      var aria2 = new Aria2Client(this.selectedServer)

      Vue.prototype.aria2 = aria2 // vue

      this.aria2 = aria2

      try {
        await aria2.ready()
        this.connectionStatus = "已连接"
      } catch (e) {
        this.connectionStatus = "连接失败!!!"
      }
    },
  },
  created() {
    this.selectedServer = this.servers[0]
    this.switchServer()
  },
  watch: {
    async aria2() {
      clearInterval(this.intervalId)
      this.globalStat = await this.getGlobalStat()
      this.startIntervalUpdate()
    },
  },
  async mounted() {
    console.log("app mounted/")


        // 10-11
    // data.method === 'aria2.onXXX'
    // 触发一次
    // this.aria2.onDownloadStart = function () {
    //   console.log("有任务开始下载了", task.gid)
    // }
    // 监听 开始/完成
    this.aria2.on("DownloadStart", async (task) => {
      console.log("有任务开始下载了", task.gid)
      this.globalStat = await this.aria2.getGlobalStat()
    })
    this.aria2.on("DownloadComplete", async (task) => {
      console.log("有任务完成", task.gid)
      this.globalStat = await this.aria2.getGlobalStat()
    })

    this.globalStat = await this.aria2.getGlobalStat() // 每次调用websocket
    this.intervelId = setInterval(async () => {
      try {
        this.globalStat = await this.aria2.getGlobalStat()
      } catch (e) {}
    })
  },
}
</script>
