/* eslint-disable prettier/prettier */ /* eslint-disable */

<style lang="less" scoped>
ul {
  list-style: none;
}
li {
  display: flex;
  cursor: pointer;
  &:selected {
    background-color: #ddd;
  }
  &:hover {
    // lang="less"
    background-color: grey;
  }
}
</style>

<template>
  <div class="home">
    <h2>下载中</h2>
    <div>
      <button @click="execSelected('pause')">暂停</button>
      <button @click="execSelected('unpause')">开始</button>
      <button @click="execSelected('remove')">删除</button>
      <button @click="selectedAll">全选</button>
      搜索 <input type="text" v-model="searchText" />
      <!-- <button @click="pauseSelected">暂停</button>
      <button @click="unpauseSelected">开始</button>
      <button @click="removeSelected">删除</button>
      <button @click="selectedAll">全选</button> -->
    </div>

    <ul>
      <li
        v-for="task of visibleTasks"
        :class="{ selected: selected.includes(task) }"
        :key="task.gid"
        @click="toggleSelect(task)"
      >
        <input type="checkbox" :checked="selected.includes(task.gid)" />
        <span>{{ getFilename(task) }} | {{ task.gid }}</span>
        |
        <span>{{ getPercent(task) | fixed }} % </span>
        <!-- / ((task.completedLength / task.totalLength) * 100) -->
        |
        <span>{{ (task.downloadSpeed / 1024) | fixed }} k/s</span>
        |
        <span> {{ task.status }}</span>
        |
        <button @click.stop="goDetail(task)">详情</button>
        <router-link
          @click.native.stop
          to="{name:'TaskDetail', params: {gid: task.gid}}"
        >
          详情
        </router-link>
      </li>
    </ul>

    <div>
      当前排序列 {{ sortColumn }}{{ sortColumn.label }} - {{ sortColumn.order }}
    </div>
    <el-table
      @sort-change="sortChange"
      :data="visibleTasks"
      style="width: 100%"
    >
      <el-table-column prop="gid" label="名称" sortable width="180">
      </el-table-column>
      <el-table-column prop="completedLength" label="进度" sortable width="180">
      </el-table-column>
      <el-table-column :formatter="getSpeed" label="速度" sortable width="180">
      </el-table-column>
      <el-table-column prop="status" label="状态" sortable width="180">
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "Downloading",
  props: ["aria2"],
  data() {
    return {
      tasks: [],
      // selected: new Map(),
      selected: [],
      searchText: "",
      sortColumn: "",
    }
  },
  components: {},
  async mounted() {

      // 10-11
       // 监听 开始/完成
    this.aria2.on("DownloadStart", (e) => {
      this.updateList()
    })
    this.aria2.on("DownloadComplete", (e) => {
      this.updateList()
    })

    console.log("down mounted//", this.aria2)
    this.tasks = [
      ...(await this.aria2.tellActive()),
      ...(await this.aria2.tellWaiting(0, 100)),
    ]
    // this.intervalId = setInterval(async () => {
    //   // this.tasks = await window.aria2.tellActive()
    //   // this.tasks = await window.aria2.tellWaiting()
    //   this.tasks = [
    //     ...(await this.aria2.tellActive()),
    //     ...(await this.aria2.tellWaiting(0, 100)),
    //   ]
    // }, 1000)
  },
  updated() {
    this.updateList()
  },
  watch: {
    aria2() {
      this.tasks = []
      this.updateList()
    },
  },
  computed: {
    // sortedTasks() {
    //   if (this.sortType == '1') {

    //   }
    //   if (this.sortType == '2') {

    //   }
    //   if (this.sortType == '-1') {

    //   }
    //   return 1
    // },
    visibleTasks() {
      // console.log('visible,',this.tasks.map(it =>it.files[0].path.toLowerCase().includes(this.searchText.toLowerCase())))
      if (this.searchText == "") {
        return this.tasks
      } else {
        return this.tasks.filter((it) => {
          return it.files[0].path
            .toLowerCase()
            .includes(this.searchText.toLowerCase())
        })
      }
    },
  },
  methods: {
    sortChange({ column, prop, order }) {
      this.sortColumn = column
    },
    getSpeed(row) {
      return ((row.completedLength / row.totalLength) * 100).toFixed(2) + "%"
    },
    goDetail(task) {
      this.$router.push("/task/" + task.gid)
    },
    async updateList() {
      try {
        this.tasks = [
          ...(await this.aria2.tellActive()),
          ...(await this.aria2.tellWaiting(0, 100)),
        ]
        // this.tasks = [
        //   ...(await window.aria2.tellActive()),
        //   ...(await window.aria2.tellWaiting(0, 100)),
        // ]
      } catch (e) {
        this.tasks = []
      }
    },
    async execSelected(action) {
      // 对选中的任务执行 暂停 恢复 删除

      if (action == "remove") {
        await this.$confirm("确定删除?")
      }
      //
      var selectedTask = this.tasks.filter((task) => {
        return this.selected.includes(task.gid)
      })
      for (let task of selectedTask) {
        try {
          if (action == "pause" && task.status == "paused") {
            continue
          }
          if (action == "unpause" && task.status == "active") {
            continue
          }
          await this.aria2[action](task.gid)
        } catch (e) {
          return
        }
      }

      for (let gid of this.selected) {
        try {
          await this.aria2[action](gid)
        } catch (e) {
          this.$alert(e.message).then(() => {
            console.log("xxx")
          })
        }
      }
      this.updateList()
    },
    pauseSelected() {
      for (let gid of this.selected) {
        window.aria2.pause(gid)
      }
    },
    unpauseSelected() {
      for (let gid of this.selected) {
        window.aria2.unpause(gid)
      }
    },
    removeSelected() {
      for (let gid of this.selected) {
        window.aria2.remove(gid)
      }
    },
    selectedAll() {
      if (this.selected.length == this.tasks.length) {
        this.selected = []
      } else {
        this.selected = this.tasks.map((it) => it.gid)
      }
    },
    toggleSelect(task) {
      var idx = this.selected.indexOf(task.gid)
      if (idx == -1) {
        this.selected.push(task.gid)
      } else {
        this.selected.splice(idx, 1)
      }
    },
    // toggleSelect(task) {
    //   if (this.selected.has(task.gid)) {
    //     this.selected.delete(task.gid)
    //   } else {
    //     this.selected.set(task.gid, true)
    //   }
    // },
    getFilename(task) {
      if (task.files?.[0].path) {
        return task.files[0].path.split("/").at(-1)
      }
      return task.files?.uris?.[0]?.uri.split("/").at(-1) ?? "未获取名称"
    },
    getPercent(task) {
      //获取下载进度
      if (task.completedLength == 0) {
        return "0"
      }
      return (task.completedLength / task.totalLength) * 100
    },
  },
  beforeDestroy() {
    clearInterval(this.intervalId)
  },
}
</script>
